<template>
    <div class="container-publish">
        <el-card>
            <div slot="header">
                <my-bread>{{$route.query.id ? '' : ''}}</my-bread>
            </div>
            <el-form :model="ruleForm" :rules="articleRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label=":" prop="title" >
                    <el-input v-model="ruleForm.title" placeholder="" style="width:400px;"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="content" >
                      <quill-editor v-model="ruleForm.content" :options="editorOption" @blur="checkContent()" />
                </el-form-item>
                <el-form-item label=":" prop="cover.type" >
                    <el-radio-group v-model="ruleForm.cover.type" @change="ruleForm.cover.images=[]" >
                        <el-radio :label="1"></el-radio>
                        <el-radio :label="3"></el-radio>
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="-1"></el-radio>
                    </el-radio-group>
                    
                    <!-- <my-image :value="testImgUrl" @input="testImgUrl = $event"></my-image> -->
                    <div style="margin-top:10px" v-if="ruleForm.cover.type === 1">
                        <my-image v-model="ruleForm.cover.images[0]" @confirm="checkCover()"></my-image>
                    </div>
                    
                    <div style="margin-top:10px" v-if="ruleForm.cover.type === 3">
                        <my-image v-for="i in 3" :key="i" v-model="ruleForm.cover.images[i-1]" @confirm="checkCover()"></my-image>
                    </div>
                    
                </el-form-item>
                <el-form-item label=":" prop="channel_id">
                    <my-channel v-model="ruleForm.channel_id"> </my-channel>
                </el-form-item>
                <el-form-item v-if="$route.query.id">
                    <el-button @click="edit()" type="success"></el-button>
                </el-form-item>    
                <el-form-item v-else>
                    <el-button @click="save(false)" type="primary"></el-button>
                    <el-button @click="save(true)" plain></el-button>
                </el-form-item>
                
            </el-form>
            <!-- {{$route.query.id}} -->
        </el-card>
    </div>
</template>
<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'

export default {
    name: 'page-publish',
    components: {
        quillEditor
    },
    data(){
        const validCoverFn = (rule, value, callback) =>{
            const images = this.ruleForm.cover.images
            if(value === 1){
                if(images[0] && images.length === 1){
                    callback()
                } else {
                    callback(new Error(''))
                }
            } else if(value === 3){
                if(images[0] && images[1] && images[2]){
                    callback()
                } else{
                    callback(new Error(''))
                }
            }else {
                callback()
            }
        }
        return {
            ruleForm:{
                id: null,
                title: null,
                content: null,
                cover:{
                    type: 1,
                    images: [],
                },
                channel_id:null,
                editorOption: {}
            },
            articleRules:{
                title: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 300, message: ' 5  300 ', trigger: 'blur' }
                ],
                content: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                channel_id:[
                    { required: true, message: '', trigger: 'change' }
                ],
                'cover.type': [
                    {
                        validator: validCoverFn, trigger: 'blur'
                    }
                ],
            },
            options: [],
            value: '',
            imageUrl: '',
                // 
            editorOption: {
                placeholder: '',
                modules: {
                toolbar: [
                    ['bold', 'italic', 'underline', 'strike'],
                    ['blockquote', 'code-block'],
                    [{ header: 1 }, { header: 2 }],
                    [{ list: 'ordered' }, { list: 'bullet' }],
                    [{ indent: '-1' }, { indent: '+1' }],
                    ['image']
                ]
                }
            },
            // testImgUrl: null,
        }
    },
    
    created(){
        if(this.$route.query.id){
            this.getAriticle()
        }  
    },
    watch: {
        '$route.query.id': function(){
            if(this.$route.query.id){
                this.getAriticle()
            } else {
                this.$refs.ruleForm.resetFields()
                this.ruleForm.cover.images = []
            }
        }
    },
    methods:{
        save(draft){
            this.$refs.ruleForm.validate( async (valid) => {
                if(valid){
                    try {
                        await this.$http.post(`articles?draft=${draft}`, this.ruleForm)
                        this.$message.success(draft ? '': '')
                        this.$router.push('/article')
                    } catch (error) {
                        this.$message.error(draft ? '': '')
                    }
                }
            })
        },
        async getAriticle(){
            const res = await this.$http.get(`articles/${this.$route.query.id}`)
            this.ruleForm = res.data.data
        },
        checkContent(){
            //  contentcontent
            //  validateField('') 
            this.$refs.ruleForm.validateField('content')
        },
        checkCover(){
            this.$refs.ruleForm.validateField('cover.type')
        },
        edit(){
             this.$refs.ruleForm.validate( async (valid) => {
                if(valid){
                    try {
                        await this.$http.put(`articles/${this.$route.query.id}?draft=false`, this.ruleForm)
                        this.$message.success('')
                        this.$router.push('/article')
                    } catch (error) {
                        this.$message.error('')
                    }
                }
            })
        }
    }
  }
</script>

<style scoped lang="less">
.container-publish{
     .image-slot{
        .image-error{
            border: 1px dashed #ddd;
            margin-right: 20px;
            width: 150px;
            height: 150px;
        }
    }
    //::v-deep 
    ::v-deep .ql-editor{
        height: 250px;
    }
    ::v-deep .ql-toolbar.ql-snow{
        padding: 0 8px;
    }
}
   
    
</style>